.playlistP-playlistImage {
	height: 100%;
	width: 100%;
	background-size: contain;
	background-origin: border-box;
	background-position: center center;
	background-repeat: no-repeat;
}
.playlistP-playlistImage-cont {
	height: 20vh;
}
@media screen and (max-width: 964px) {
	.playlistD {
		justify-content: center !important;
		align-items: center !important;
	}
}
.tracks-heading {
	border-radius: 10px;
}
/* Dark Mode */
.playList-light .playlist-full-cont {
	background: rgb(0, 0, 0);
	background: linear-gradient(
		180deg,
		rgba(0, 0, 0, 1) 0%,
		rgba(31, 31, 31, 1) 35%,
		rgba(0, 0, 0, 1) 100%
	);
}
.playList-light .tracks-heading {
	background-color: rgb(27, 27, 27);
}

/* Light Mode */
.playList-dark .playlist-full-cont {
	background: rgb(0, 0, 0);
	background: linear-gradient(
		100deg,
		rgb(1, 1, 1) 0%,
		rgba(31, 31, 31, 1) 35%,
		rgb(2, 2, 2) 100%
	);
}
.playList-dark .tracks-heading {
	background-color: rgb(49, 49, 49);
}
