/* Base */
.left-logo-cont {
	/* background-color: brown; */
	padding: 20px;
	background-color: #00000007;
}
.left-top-cont {
	padding: 20px;
	margin-bottom: 20px !important;
}
.left-top-contents {
	/* background-color: rgb(210, 185, 233); */
	padding: 10px;
}

.left-top-contents-text {
	justify-self: center;
	margin-left: 20px !important;
	align-self: center;
}

.leftList-cont {
	margin-top: 20px !important;
	overflow-y: scroll;
}
.left-bot-cont {
	/* background-color: cadetblue; */
	min-height: 75%;
}
.left-bot-contents {
	padding: 10px;
}
.left-bot-contents-text {
	justify-self: center;
	margin-left: 20px !important;
	align-self: center;
	font-weight: 200 !important;
}
.left-bot-contents-text:hover {
	color: white;
}
.spotify-logo {
	height: auto;
	max-width: 200px;
}

/* Light Mode */
.left-cont-dark {
	background-color: black;
	color: white;
}
.left-cont-light .left-bot-contents-text:hover {
	color: rgb(0, 0, 0);
}
.left-cont-light .left-bot-contents-text {
	color: rgb(105, 105, 105);
}
.slogodark {
	filter: grayscale(100%) brightness(200%);
}
.left-cont-dark .left-top-cont {
	border-bottom: 1px solid white;
}
.left-cont-light .left-top-cont {
	background-color: black;
	color: white;
}
/* Dark Mode */
.left-cont-light {
	background: rgb(255, 255, 255);
	background: linear-gradient(
		90deg,
		rgba(255, 255, 255, 1) 0%,
		rgba(238, 238, 238, 1) 47%,
		rgba(255, 255, 255, 1) 100%
	);
	color: black;
}
.left-cont-dark .left-top-cont {
	background-color: rgb(255, 255, 255);
	color: rgb(0, 0, 0);
}
.left-cont-dark .left-bot-contents-text:hover {
	color: rgb(255, 255, 255);
}

.left-cont-dark .left-bot-contents-text {
	color: rgb(175, 175, 175);
}
.slogolight {
	filter: grayscale(100%) brightness(0%);
}
@media screen and (max-width: 855px) and (min-width: 600px) {
	.left-top-contents-text {
		font-size: 0.7em !important;
		margin-left: 5px !important;
	}
}
